
.wp-block-citeo-jeunesse-section-download {
    display: flex;
    padding: 60px 65px;
    flex-direction: column;
    align-items: center;
    align-self: stretch;
    @media screen and (max-width: 834px) {
        padding: 20px 0px;
    }
    .ecojunior-content--heading {
        display: flex;
        width: 90%;
        gap: 1rem;
        padding-bottom: 1rem;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        border-bottom: 1px solid var(--ds-semantic-color-layout-border-strong-default, #D1D5DB);
        .wp-block-heading {
            font-family: "Bricolage Grotesque";
            text-align: center;
        }
    }
    .ecojunior-content--accordeon {
        background-color: #fff;
        width: 90%;
        position: relative;
        padding-bottom: 6rem;
        &>article.accordeon-wrapper {
            padding-right: 24rem;
            @media screen and (max-width: 834px) {
                padding-right: 0;
            }
            .accordeon-toggle .wp-element-button::before {
                background-color: #093C1C;
            }
        }
        .no-result-txt.Body-2 {
            padding-top: 1rem;
            text-align: center;
        }
        .wp-block-image {
            position: absolute;
            right: 0;
            top: 1.5rem;
            img {
                max-width: 365px;
            }
        }
        .accordeon-wrapper.is-active {
            .accordeon-toggle {
                display: none;
            }
            .accordeon-content {
                min-height: 16rem;
            }
        }
        .accordeon-toggle.is-style-tertiary {
            .wp-block-button__link {
                text-decoration: none !important;
                text-align: left;
                border-radius: 0;
                color: #093C1C;
                width: 100%;
                padding-left: 0;
                border-bottom: 1px solid var(--ds-semantic-color-layout-border-strong-default, #D1D5DB);
                background-color: #fff;
                &:hover {
                    background-color: #fff;
                    color: #093C1C;
                }
                &::before,
                &::after {
                    background-color: #093C1C !important;
                }
            }
        }
        .accordeon-content {
            border-bottom: 1px solid var(--ds-semantic-color-layout-border-strong-default, #D1D5DB);
            &.is-layout-flex {
                align-items: flex-start;
            }
            .wp-block-heading {
                color: #093C1C;
                margin-top: .3rem;
                font-weight: 600;
            }
            .Body-2 {
                margin-bottom: 2rem;
            }
            .wp-block-button.add-download {
                .wp-block-button__link {
                    background-color: #fff;
                    flex-direction: row-reverse;
                    text-decoration: none !important;
                    border: 1px solid var(--ds-semantic-color-action-border-stronger);
                    color: var(--ds-semantic-color-action-content-stronger) !important;
                    &:hover {
                        background-color: #fff !important;
                    }
                    &::before {
                        background-color: var(--ds-semantic-color-action-surface-stronger) !important;
                    }
                }
            }
        }
    }
    .ecojunior-content--footer {
        background: #0d3a22;
        color: #fff;
        padding: 3rem 1.5rem 2.5rem 1.5rem;
        margin-top: 3rem;
        position: relative;
        text-align: center;
        width: 100%;
        &::before {
            content: "";
            display: block;
            position: absolute;
            top: -71px;
            left: 0;
            width: 100%;
            height: 80px;
            background: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" width="1040" height="116" viewBox="0 0 1040 116" fill="none"><path d="M1040 31.785C974.991 31.785 973.873 69.805 908.865 69.805C862.866 69.805 803.043 0.751953 738.058 0.751953C651.774 0.751953 648.324 49.266 565.516 49.266C467.169 49.266 455.002 26.819 389.99 26.819C324.979 26.819 306.716 60.489 259.994 60.489C195.138 60.489 195.138 38.042 129.997 38.042C84.1308 38.042 40.989 66.102 1.64068e-06 66.102L2.88719e-06 115.752L1040 115.752L1040 31.785Z" fill="%23093C1C"/></svg>') no-repeat top center/cover;
            z-index: 1;
            pointer-events: none;
        }
        .Body-2,
        .Body-3,
        .Body-1 {
            color: #fff;
            font-size: 1rem;
            margin-bottom: 1.2rem;
            &:first-child {
                text-transform: uppercase;
                letter-spacing: 0.1em;
                font-size: 0.95rem;
                font-weight: 600;
                margin-bottom: 1.5rem;
                color: #e6f7e6;
            }
            &:last-child {
                font-size: 1.1rem;
                color: #e6f7e6;
                margin-bottom: 2rem;
            }
        }
        .wp-block-heading {
            margin: 0;
            font-weight: 700;
            line-height: 1.2;
            background: none;
            color: #fff;
            font-family: "Bricolage Grotesque";
            &:first-of-type {
                font-size: 2rem;
                margin-bottom: 0.5rem;
            }
            &:nth-of-type(2) {
                font-size: 2rem;
                margin-bottom: 1.5rem;
                span {
                    color: #ffb800;
                    font-weight: 800;
                    display: inline;
                }
            }
        }
        .wp-block-heading:nth-of-type(2),
        .wp-block-heading strong,
        .wp-block-heading span {
            color: #ffb800;
            font-weight: 800;
        }
        .discover-button-wrapper {
            margin-top: 2rem;
            .wp-block-button {
                margin: 0 auto;
                .wp-block-button__link {
                    background: #fff;
                    color: var(--Color-Header);
                    border-radius: 8px;
                    font-weight: 700;
                    font-size: 1.1rem;
                    padding: 0.9rem 2.5rem;
                    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.08);
                    &:hover {
                        background: #ffb800;
                        color: #0d3a22;
                    }
                }
            }
        }
    }
    .accordeon-wrapper.is-active .accordeon-content {
        padding: 1rem 0;
    }
    .dynamic-year-slider {
        position: relative;
        padding: 0 5rem;
        width: 7.5rem;
        overflow: hidden;
        .slider-wrap {
            width: 100%;
            margin: auto;
            .item-year {
                flex-shrink: 0;
                display: block;
                padding: .6rem .8rem 1rem .8rem;
                font-size: 1.2rem;
                font-weight: 700;
                white-space: nowrap;
                color: #0d3a22;
                opacity: 0;
                cursor: pointer;
                transition: opacity .25s ease-out;
                &.active-slide {
                    opacity: 1;
                }
            }
        }
        .slider-controls {
            margin: 0;
            .prev-slide,
            .next-slide {
                display: block !important;
                position: absolute;
                top: 0;
                .wp-element-button {
                    border-radius: 5.9rem;
                    border: solid 1px var(--ds-core-color-gray-300);
                    background-color: var(--ds-core-color-white);
                    &::after {
                        background-color: #1F2937;
                    }
                }
                &.disabled {
                    opacity: 1;
                    .wp-element-button {
                        background-color: var(--ds-core-color-gray-50);
                        border-color: transparent;
                        &::after {
                            background-color: #9CA3AF;
                        }
                    }
                }
            }
            .prev-slide {
                left: 0;
                .wp-element-button {
                    padding: .5rem 1rem;
                    &::after {
                        mask-image: url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" width="24" height="25" viewBox="0 0 24 25" fill="none"><path fill-rule="evenodd" clip-rule="evenodd" d="M15.7071 6.04485C16.0976 6.43537 16.0976 7.06854 15.7071 7.45906L10.4142 12.752L15.7071 18.0448C16.0976 18.4354 16.0976 19.0685 15.7071 19.4591C15.3166 19.8496 14.6834 19.8496 14.2929 19.4591L8.29289 13.4591C7.90237 13.0685 7.90237 12.4354 8.29289 12.0448L14.2929 6.04485C14.6834 5.65432 15.3166 5.65432 15.7071 6.04485Z" fill="%239CA3AF"/></svg>');
                    }
                }
            }
            .next-slide {
                right: 0;
                .wp-element-button {
                    padding: .5rem 1rem;
                    &::after {
                        mask-image: url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" width="24" height="25" viewBox="0 0 24 25" fill="none"><path fill-rule="evenodd" clip-rule="evenodd" d="M8.29289 6.04485C8.68342 5.65432 9.31658 5.65432 9.70711 6.04485L15.7071 12.0448C16.0976 12.4354 16.0976 13.0685 15.7071 13.4591L9.70711 19.4591C9.31658 19.8496 8.68342 19.8496 8.29289 19.4591C7.90237 19.0685 7.90237 18.4354 8.29289 18.0448L13.5858 12.752L8.29289 7.45906C7.90237 7.06854 7.90237 6.43537 8.29289 6.04485Z" fill="%231F2937"/></svg>');
                    }
                }
            }
        }
    }


    .accordeon-wrapper {
        .accordeon-toggle {
            .wp-element-button {
                position: relative;
                justify-content: flex-start;
                padding: 1.5rem 2rem 1.5rem 0;
                &::before,
                &::after {
                    content: '';
                    position: absolute;
                    right: .5rem;
                    top: calc(50% - 1px);
                    width: 1.05rem;
                    height: 2px;
                    background-color: var(--ds-semantic-color-accent1-surface-medium);
                    border-radius: 1px;
                    mask-image: none !important;
                    background-image: none !important;
                }
                &::after {
                    transform: rotateZ(90deg);
                    transition: ease-out .25s;
                }
            }
        }
        .accordeon-content {
            max-height: 0;
            opacity: 0;
            pointer-events: none;
            transition: max-height ease-out .15s .4s, opacity ease-out .25s;
            p {
                font-size: 1rem;
                &.mag-date.Body-3 {
                    font-size: .875rem;
                    text-transform: capitalize;
                }
            }
        }
        &.is-active {
            .accordeon-toggle .wp-element-button::after {
                transform: rotateZ(0deg);
                opacity: 0;
            }
            .accordeon-content {
                transition: max-height ease-out 1s, opacity ease-out .25s .25s;
                max-height: 3000px;
                overflow: hidden;
                opacity: 1;
                pointer-events: all;
            }
        }
        @media screen and (max-width: 834px) {
            &:not(:first-child) {
                border-top: 0;
            }
            .wp-block-image.mag-featured-img {
                display: none;
            }
            &.is-active .accordeon-content {
                min-height: 13rem;
            }
        }
    }
    .ecojunior-skeleton {
        display: flex;
        width: 100%;
        min-height: 180px;
        align-items: stretch;
        gap: 2rem;
        padding: 2rem 0;
        .skeleton-left {
            flex: 1 1 0;
            display: flex;
            flex-direction: column;
            gap: 1rem;
            justify-content: center;
        }
        .skeleton-line {
            &:first-child {
                height: 158px;
            }
            height: 64px;
            width: 100%;
            background: #efeded;
            border-radius: 6px;
            animation: skeleton-loading .8s infinite linear alternate;
            @media screen and (max-width: 834px) {
                width: 100%;
            }
        }
        .skeleton-right {
            flex: 0 0 322px;
            height: 322px;
            background: #efeded;
            border-radius: 10px;
            align-self: center;
            animation: skeleton-loading .8s infinite linear alternate;
            @media screen and (max-width: 834px) {
                display: none;
            }
        }
    }
    @keyframes skeleton-loading {
        0% {
            opacity: 1;
        }
        100% {
            opacity: 0.5;
        }
    }


}
